form {
  border: none;
}

fieldset {
  border: none;
}

.search-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.search-box {
  border: none;
  padding: 0.75rem;
  border-radius: 1rem;
  margin-right: 0.5rem;
}

.search-box::placeholder {
  color: darkblue;
}

.search-box:focus {
  outline: none;
}

.search_icon {
  width: 15px;
  height: 15px;
}

.search_icon path {
  fill: white;
}

@media screen and (max-width: 450px) {
  form {
    width: 100%;
    margin-top: -5px;
  }

  fieldset {
    margin: 0;
    padding: 0 0 0.5rem 0;
  }

  input {
    text-align: center;
  }
}
